import { IUpdatable, ISceneObject } from "../scene/IScene.js";
import { Move } from "../utils/MovementUtils.js";

export class FollowingAI implements IUpdatable {
    public target?: ISceneObject;

    protected readonly _obj: ISceneObject;
    protected readonly _speed: number;

    public constructor(controlledObject: ISceneObject, speed: number, target?: ISceneObject) {
        this._obj = controlledObject;
        this._speed = speed;
        this.target = target;
    }

    public update(diff: number): void {
        if (!this.target)
            return;

        this._obj.position = Move.to(this._obj.position, this.target.position, this._speed * diff / 1000);
    }
}